import Vue from 'vue'

//  `v-focus`
var  basImagePath = 'http://p024s277s.bkt.clouddn.com/';
Vue.directive('src', {
  
    //  DOM 
    inserted: function (el,binding) {
      // 
      el.src = basImagePath+binding.value;
    },
    update: function (el,binding) {
      // 
      el.src = basImagePath+binding.value;
    }
  })


